        <th>Скидка</th>
        <th>Регион</th>
        <th>Файлы</th>
      </tr>
    </thead>
    <tbody ref="table" v-if="offers.length">
      <tr v-for="(offer, i) in offers" :key="i">
        <td class="no-wrap">
