            <input
              type="checkbox"
              :id="offer.number"
              :name="offer.number"
              :value="offer.number"
              :disabled="
                offerToCopy.length && !offerToCopy.includes(offer.number)
              "
              v-model="offerToCopy"
            />
            <label :for="offer.number"></label>
          </div>
        </td>
        <td>
          <span>
