          </span>
          <span class="tooltip">
            {{ offer.region }}
          </span>
        </td>
        <td class="no-wrap">
          <div class="offer-actions">
            <div class="offer-action _pdf">
              <img src="@/assets/icons/offer-actions/pdf.svg" alt="PDF" />
            </div>
            <div class="offer-action _excel">
              <img src="@/assets/icons/offer-actions/excel.svg" alt="Excel" />
            </div>
