              <img src="@/assets/icons/offer-actions/ru.svg" alt="Ru" />
            </div>
            <div class="offer-action _tz">
              <img src="@/assets/icons/offer-actions/tz.svg" alt="Tz" />
              <div class="offer-action-dropdown">
                <div class="offer-action-dropdown-content">
                  <div class="base-check radio">
                    <input
                      type="radio"
                      id="ktru"
                      value="ktru"
                      v-model="downloadType"
                    />
                    <label for="ktru" class="upp"> ктру </label>
                  </div>
                  <div class="base-check radio">
                    <input
                      type="radio"
                      id="tu"
                      value="tu"
                      v-model="downloadType"
                    />
                    <label for="tu" class="upp"> ту </label>
                  </div>
                  <div class="base-check radio">
                    <input
                      type="radio"
                      id="ktrus"
                      value="ktrus"
                      v-model="downloadType"
                    />
                    <label for="ktrus"
                      ><span class="upp">ктру</span> сокр.
                    </label>
                  </div>
                  <base-button
                    :type="'filled'"
                    @clicked="download(offer.id, downloadType)"
                  >
                    Скачать
                  </base-button>
                </div>
              </div>
            </div>
          </div>
        </td>
      </tr>
    </tbody>
  </table>
  <div
    class="offer-table-mobile table-mobile"
    ref="table"
    v-else-if="offers.length && $isMobile"
  >
    <div class="table-mobile-column" v-for="(offer, i) in offers" :key="i">
      <div class="offer-table-mobile-checkbox">
        <div class="base-check radio">
          <input
            type="checkbox"
            :id="offer.number"
            :name="offer.number"
            :value="offer.number"
            :disabled="
              offerToCopy.length && !offerToCopy.includes(offer.number)
            "
            v-model="offerToCopy"
          />
          <label :for="offer.number"></label>
        </div>
