        </span>
      </div>
    </div>
  </div>
  <span class="offer-content-notFound" v-else> Ничего не найдено </span>
</template>

<script>
import BaseButton from "@/components/common/Base.Button.vue";

export default {
  name: "OfferTable",
  props: {
    offers: {
      type: Array,
    },
  },
  components: {
    BaseButton,
  },
  data() {
    return {
      offerToCopy: [],
      downloadType: "",
    };
  },
  methods: {
    download(id, type) {
      const params = {
        id: id,
        type: type,
      };
      this.$emit("download", params);
    },
    onScroll(el) {
      let height = el.getBoundingClientRect().height;
      let scrollHeight = el.scrollHeight;
      let top = el.scrollTop;
      this.$emit("scroll", top);
      Math.ceil(top + height) == scrollHeight.toFixed(0) &&
        this.$emit("scrolled");
    },
  },
  mounted() {
    const el = this.$refs.table;
    el &&
      el.addEventListener("scroll", () => {
        this.onScroll(el);
      });
  },
  watch: {
    offerToCopy() {
      this.$emit("copy", this.offerToCopy);
    },
  },
  emits: ["download", "scroll", "scrolled", "copy"],
};
</script>

